let enums = require("../../iuc/roomApplication/enumsv1.js");
export default {
	name: "labTimeLine",
	props: {
		timeline: {
			required: true
		}
	},
	data() {
		return {
			stepColor: ['blue', 'blue', 'green', 'green', 'red'],
			stepInfo: enums.stepInfo,
		}
	},
	methods: {
		inStep(steps, state) {
			let s = state;
			return steps.indexOf(s) > -1;
		}
	}
}
